import React from 'react'
import '../css/Card.css'


const Card = ({ img, label, text }) => {
  return (
    <div className='card'>
      <div className="image">
        <img src={img} alt='' className='img'/>
      </div>
      <div className="label">{label}</div>
      <div className="text">{text}</div>
    </div>
  )
}

export default Card
